.input{
    display:flex;
    width: 90%;
    position: relative;
    align-items: center;
    margin-bottom: 1em;
}

.input__box{
    width:100%;
    border-radius: 50px;
    padding: 1em;
    font-size: 1.4rem;
    border: none;
    transition: 0.2s;
    box-shadow: inset 0 0 5px black;
}

.input__box:focus{
    box-shadow: 0 0 10px 1000px rgba(0,0,0,0.5);
    outline:none;
}

/* Add button style */
.input__submit{
position: absolute;
right:0;
width: 50px;
height: 50px;
margin: 0.5em;
border-radius: 50px;
border:none;
font-size: 1.2rem;
background-color: #2f74c0;
color: white;
transition: 0.2s all;
box-shadow: 0 0 10px black;
}

.input__submit:hover{
    background-color: #388ae2;
}
.input__submit:active{
    transform: scale(0.8);
    box-shadow: 0 0 5px black;
}